.engineering-container {
  background-color: #f9f9f9;
  padding: 0 20%;
  background-color: #f9f9f9;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.engineering-container h2 {
  text-align: center;
  color: #333;
  margin-bottom: 20px;
}

.engineering-container p {
  text-align: justify;
  color: #555;
  margin-bottom: 20px;
  line-height: 1.6;
}

.gallery {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  gap: 20px;
}

.gallery-item {
  text-align: center;
  flex: 1 1 calc(50% - 20px); /* Adjust to ensure 2 images per row */
  max-width: calc(50% - 20px); /* Adjust to ensure 2 images per row */
}

.gallery-item img {
  width: 100%;
  height: auto;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.gallery-item p {
  margin-top: 10px;
  color: #333;
  font-weight: bold;
}
.carousel{
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  max-height: 700px;
}